import { initializeApp } from "firebase/app"
import { getFunctions } from "firebase/functions"

const firebaseConfig = {
  apiKey: process.env.PORTFOLIO_APP_FIREBASE_API_KEY,
  authDomain: process.env.PORTFOLIO_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.PORTFOLIO_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.PORTFOLIO_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.PORTFOLIO_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.PORTFOLIO_APP_FIREBASE_APP_ID,
  measurementId: process.env.PORTFOLIO_FIREBASE_MEASUREMENT_ID,
}

//init firebase
const app = initializeApp(firebaseConfig)

//init functions
const functions = getFunctions(app)

export { functions }
